/* font.css : 폰트 */
@charset "utf-8";


/* Pretendard ExtraLight */
@font-face {
  font-family: 'Pretendard';
  font-display: swap;
  font-weight: 200;
  src: local("Pretendard ExtraLight"),
       url('../fonts/Pretendard-ExtraLight.subset.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/Pretendard-ExtraLight.subset.woff') format('woff'), /* Modern Browsers */
       url('../fonts/Pretendard-ExtraLight.ttf') format('truetype'); /* Safari, Android, iOS */
  unicode-range: U+AC00-D7A3, U+0020-002F, U+003A-0040, U+005B-0060, U+007B-007E; /* 한글, 특수문자 */
}

/* Pretendard Regular */
@font-face {
  font-family: 'Pretendard';
  font-display: swap;
  font-weight: 400;
