       url('../fonts/Pretendard-Regular.subset.woff') format('woff'), /* Modern Browsers */
       url('../fonts/Pretendard-Regular.ttf') format('truetype'); /* Safari, Android, iOS */
  unicode-range: U+AC00-D7A3, U+0020-002F, U+003A-0040, U+005B-0060, U+007B-007E; /* 한글, 특수문자 */
}

