/* 맑은고딕 */
@font-face {
  font-family:MalgunGothic;
  src: local("MalgunGothic"),
       url('../fonts/MalgunGothic.eot'), /* IE9 Compat Modes */
       url('../fonts/MalgunGothic.eot?#iefix') format('embedded-opentype'); /* IE6-IE8 */
}



$font-set: 'Pretendard', 'Roboto', '맑은 고딕', 'MalgunGothic', 'Dotum', '돋움', sans-serif !important;

body {
  font-family: $font-set;
}

* {
  font-family: inherit !important;
}
